//Create the tab component
import React, { useState } from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import "../tabs/tabs.styles.css"
//creating TabPanel to show the tab's content
const TabPanel = (props) => {
    const { children, value, index, ...other } = props;

    return (
        <div
            style={{ height: '100%', overflow: 'hidden' }}
            role="tabpanel"
            hidden={value !== index}
            id={`tabpanel-${index}`}
            aria-labelledby={`tab-${index}`}
            {...other}
        >
            {value === index && (
                <div styles={{ padding: 0, margin: 0, }}>
                    <div>{children}</div>
                </div>
            )}
        </div>
    );
};
//creating the screen with the tabs
const ScreenWithTabs = ({ selectedTab, setSelectedTab, tabLabels, ...props }) => {

    const handleTabChange = (event, newValue) => {
        setSelectedTab(newValue);
    };
    //creating Tabs
    return (
        <div>
            <Tabs className='smallTabs' value={selectedTab} variant="scrollable" scrollButtons="auto" onChange={handleTabChange} aria-label="tabs example">
                <Tabs className='smallTabs' value={selectedTab} variant="scrollable" scrollButtons="auto" onChange={handleTabChange} aria-label="tabs example">
                    {tabLabels.map((label, index) => (
                        <Tab key={index} label={label} />
                    ))}
                </Tabs>

            </Tabs>

        </div>
    );
};

export default ScreenWithTabs;
export { TabPanel };