import Table from "../table/table.component";
import { useState } from 'react';
import { Modal } from "@mui/material";
import { CMButton, CMDiv, CMSection } from "./section.styles";
import { ReactComponent as ArrowIcon } from '../../../assets/icons/arrow.svg';
import { ReactComponent as SearchIcon } from "../../../assets/icons/search.svg";
import CalendarButton from "../calendar/calendar.component"
import ScreenWithTabs from "../tabs/tabs.component";

const Section = ({ isCollapsed, extraColumnsInPopup, columns, showSearch, handleSearch, showCalendar, availableDates, onDateChange, isShowModal, setIsShowModal, ...props }) => {
    if (isCollapsed === undefined) isCollapsed = true;
    if (setIsShowModal === undefined) setIsShowModal = () => { };
    if (isShowModal === undefined) isShowModal = false;
    const [open, setOpen] = useState(false);
    const [collapsed, setCollapsed] = useState(isCollapsed);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    const toggleCollapsed = () => setCollapsed(value => !value);

    const columnsTemp = extraColumnsInPopup ? [...columns, ...extraColumnsInPopup] : columns;

    return (
        <div>
            <CMButton collapsed={collapsed} onClick={toggleCollapsed} type='button' >
                <ArrowIcon width={14} height={14} />
            </CMButton>
            {showCalendar && (
                <CMButton type="button">
                    <CalendarButton availableDates={availableDates} onDateChange={onDateChange} />
                </CMButton>
            )}

            {showSearch &&
                <CMButton className="search" onClick={handleSearch}>
                    <SearchIcon width={16} height={16} />
                </CMButton>
            }
            <SectionContent setIsShowModal={setIsShowModal} handleOpen={handleOpen} collapsed={collapsed} columns={columns} {...props} />
            <Modal open={open || isShowModal} onClose={() => { handleClose(); setIsShowModal(false) }} sx={{ backgroundColor: '#fff' }}>

                <CMDiv>
                    <button className="close-modal" onClick={() => { handleClose(); setIsShowModal(false) }}>X</button>
                    {showCalendar && (
                        <CMButton type="button">
                            <CalendarButton availableDates={availableDates} onDateChange={onDateChange} />
                        </CMButton>
                    )}

                    <SectionContent handleOpen={handleOpen} collapsed={collapsed} columns={columnsTemp} {...props} />
                </CMDiv>
            </Modal>
        </div>
    )
}

const SectionContent = ({ title, maxHeight, handleOpen, collapsed, dropdown, isTab, setSelectedTab, selectedTab, tabLabels, setIsShowModal, ...props }) => {
    return (
        <CMSection maxHeight={maxHeight} >
            <div className="panel-heading">
                <h4 className="panel-title">
                    <a onClick={() => { handleOpen(); setIsShowModal(true) }}>{title}</a>
                </h4>
            </div>
            <div className={`${collapsed ? 'collapsed' : ''}`}>

                {isTab ? (
                    <>
                        <ScreenWithTabs tabLabels={tabLabels} setSelectedTab={setSelectedTab} selectedTab={selectedTab} />
                        <div style={{ height: '89%', overflow: 'hidden' }}>
                            <Table {...props} />
                        </div>
                    </>
                ) : (
                    <Table {...props} />
                )}
            </div>
        </CMSection>
    )
}

export default Section;