import styled from "styled-components";
import { TextField, styled as muiStyled } from "@mui/material"
export const Calendar = styled.img(props => ({
    cursor: 'pointer',
    width: '24px',
    height: '24px',

}));
export const PagSection = styled.div(props => ({
    display: 'flex',
    justifyContent: 'center',
    marginTop: '8px'
}));
export const CMTextField = muiStyled(TextField)({
    margin: '8px',
    width: '150px',
});
export const CButton = styled.button(props => ({
    backgroundColor: 'unset',
    border: 'unset',
    cursor: 'pointer',
    float: 'right',
    outline: '0 !important',

    'svg': {
        transition: 'all 0.5s ease-out',
        transform: !props.collapsed ? 'rotate(0deg)' : 'unset',
    },
}));