import { esES, GridToolbarContainer, GridToolbarFilterButton } from '@mui/x-data-grid-pro';
import { useState } from 'react';
import { CMDataGrid, CMGridToolbarQuickFilter } from './table.styles';

function CustomToolbar({ setFilterButtonEl }) {
    return (
        <>
            <GridToolbarContainer>
                <GridToolbarFilterButton ref={setFilterButtonEl} />

                <CMGridToolbarQuickFilter placeholder='Buscar documento' />
            </GridToolbarContainer>
        </>
    );
}

function CustomNoRowsOverlay(message) {
    if (!message) message = 'No se encontraron resultados';
    return (
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100%' }}>
            <h4 style={{ color: '#333333' }}>{message}</h4>
        </div>
    )
}


const Table = ({ rows, columns, hiddenColumns, onRowClick, filterValue,
    showToolBar = true, hidePagination = false, singleColumn = false
    , emptyMessage, customRowId = 'id' }) => {
    const [filterButtonEl, setFilterButtonEl] = useState(null);

    const renderSingleRow = (props) => {
        return (
            <div className='single-row'>
                {columns.map((column, index) => {
                    const { renderCell, field, headerName } = column;

                    return renderCell ? renderCell(props, index) : (
                        <div key={index} >
                            <span className='column-header'>{headerName}: </span>
                            {props.row[field]}
                        </div>
                    )
                })}
            </div>
        )
    }

    const singleColumnArray = [
        { field: 'col1', headerName: '', flex: 1, renderCell: renderSingleRow }
    ];

    return (
        <CMDataGrid
            experimentalFeatures={{ lazyLoading: true }}
            className={`${singleColumn ? 'single-column-grid' : ''}`}
            getRowId={(row) => row[customRowId]}
            getRowHeight={() => 'auto'}
            columnHeaderHeight={30}
            disableColumnMenu
            disableColumnSelector
            hideFooterSelectedRowCount={true}
            hideFooter={hidePagination}
            rows={rows}
            columns={singleColumn ? singleColumnArray : columns}
            initialState={{
                columns: {
                    columnVisibilityModel: hiddenColumns,
                },
                filter: {
                    filterModel: {
                        items: [],
                        quickFilterValues: [filterValue]
                    }
                }
            }}
            slots={{
                noRowsOverlay: () => CustomNoRowsOverlay(emptyMessage),
                ...(showToolBar && { toolbar: CustomToolbar })
            }}
            slotProps={{
                panel: {
                    anchorEl: filterButtonEl,
                },
                toolbar: {
                    setFilterButtonEl,
                },
            }}
            localeText={esES.components.MuiDataGrid.defaultProps.localeText}
            // pageSizeOptions={[100]}
            getRowClassName={(params) =>
                `${params.indexRelativeToCurrentPage % 2 === 0 ? 'even' : 'odd'} ${params.row.fn ? `fn-${params.row.fn}` : ''}`
            }
            onRowClick={onRowClick}
        />
    )
}

export default Table;