import * as React from 'react';
import Stack from '@mui/material/Stack';
import {
  DataGridPro,
  GRID_DETAIL_PANEL_TOGGLE_FIELD,
} from '@mui/x-data-grid-pro';
import Table from '../table/table.component';
import { CMModalTable } from './modal-table.styles';

function DetailPanelContent({ rows, columns, onRowClick }) {

  return (
    <Stack
      sx={{
        py: 0,
        height: '100%',
        boxSizing: 'border-box',
        position: 'absolute',
        left: 0,
        top: 0,
        // width,
      }}
      direction="column"
    >
      <Table 
        columns={columns}
        rows={rows}
        onRowClick={onRowClick}
        hideFooter 
      />
    </Stack>
  );
}

const SectionFilter = ({rows, columns, rowDetailsColumns, onRowClick, isOpened, ...props}) => {

  const getDetailPanelContent = React.useCallback(
    ({ row }) => <DetailPanelContent rows={row.Fracciones} columns={rowDetailsColumns} onRowClick={onRowClick} />,
    [],
  );

  const getDetailPanelHeight = React.useCallback(() => 400, []);

  return (
      <CMModalTable className={`${isOpened ? 'opened': ''}`}>
        <DataGridPro
          columns={columns}
          rows={rows}
          rowThreshold={0}
          disableColumnSelector
          disableRowSelectionOnClick
          pinnedColumns={{ left: [GRID_DETAIL_PANEL_TOGGLE_FIELD] }}
          getDetailPanelHeight={getDetailPanelHeight}
          getDetailPanelContent={getDetailPanelContent}
          sx={{
            '& .MuiDataGrid-detailPanel': {
              overflow: 'visible',
            },
            '& .MuiStack-root': {
              width: '100%'
            },
            '& .MuiDataGrid-columnHeaders': {
              display: 'none'
            },
            '& .MuiDataGrid-toolbarContainer': {
              position: 'unset',

              '& > button': {
                display: 'none'
              }
            },
            '& .MuiDataGrid-footerContainer': {
              minHeight: 'auto'
            }, 
            '& .MuiDataGrid-cell:focus': {
              outline: '0 !important'
            }
          }}
        />
      </CMModalTable>
  );
}

export default SectionFilter;