import { useContext, useEffect, useState } from "react";
import Section from "../common/section/section.component";
import { FractionDetailsContext } from "../../context/fractionDetails.context";
import DocumentLink from "../common/document-link/document-link.component";
import { ReactComponent as PDFIcon } from '../../assets/icons/pdf.svg';
import { fetchData } from "../../services/get.service";
import { FractionInitContext } from "../../context/fractionInit.context";
import ModalTable from "../common/modal-table/modal-table.component";

const DocumentColumn = (props, index) => {
    const {Documentos} = props.row;
    return <DocumentLink key={index} documents={Documentos}>
                <PDFIcon width={16} height={16} />
            </DocumentLink>

}

const columns = [
    {
        field: 'FilePath',
        headerName: '',
        minWidth: 25,
        maxWidth: 25,
        renderCell: DocumentColumn
    }, 
    {
        field: 'Acuerdo',
        headerName: 'Acuerdo',
    },
    {   
        field: 'Complemento',
        headerName: 'Complemento',
    },
    {
        field: 'Condicion',
        headerName: 'Condicion',
    },
    {
        field: 'Fundamento',
        headerName: 'Fundamento',
    },
    {
        field: 'Criterio',
        headerName: 'Criterio',
    },
    {
        field: 'DOF',
        headerName: 'DOF',
    },
    {
        field: 'Permiso',
        headerName: 'Permiso',
    },
    {
        field: 'Identificador',
        headerName: 'Identificador',
    },
];

const acuerdoColumns = [
    {field: 'Acuerdo', headerName: '', flex: 1}
];

const acuerdoDetailsColumns = [
    {field: 'd', headerName: '', flex: 1},
];

const RegulacionesExpo = () => {
    const {fractionDetailsMap, setFractionNico} = useContext(FractionDetailsContext);
    const {fractionInitMap} = useContext(FractionInitContext);
    const [rows, setRows] = useState(fractionDetailsMap);
    const [acuerdos, setAcuerdos] = useState([]);
    const [dropdownInfo, setDropdownInfo] = useState([]);

    const onRowClick = (fraction) => {
        setFractionNico(fraction.row.fn);
    }

    /*useEffect(() => {
        fetchData(`acuerdosDeImportacion`).then(response => {
            if(response) {
                setAcuerdos(response)
            }
        })
    }, [])*/

    useEffect(() => {
        if(fractionDetailsMap['regulacionesExpo']) {
            const tmpRows = fractionDetailsMap['regulacionesExpo'].map((item, index) => {
                return {id: (index+1), ...item}
            });
            setRows(tmpRows);
        } else {
            setRows([]);
        }

        
    }, [fractionDetailsMap]);

    useEffect(() => {
        // if(fractionInitMap && acuerdos) {

        //     const tmpAcuerdos = acuerdos.map((acuerdo, index) => {
                
        //         const tmpFractions = acuerdo.Fracciones.map((fraction, idx) => {

        //                     const description = fractionInitMap.filter((fr) => fr.fn === fraction.fn);

        //                     if(description && description[0] && description[0].d) {
        //                         return {...fraction, id: idx, d: `${description[0].fn}: ${description[0].d}`}
        //                     } else {
        //                         return fraction;
        //                     }
        //         });

        //         return {...acuerdo, id: index, Fracciones: tmpFractions};
        //     });

        //     setDropdownInfo(tmpAcuerdos);
        // }
    }, [fractionInitMap, acuerdos])

    return (
        <div style={{position: 'relative'}}>
            <Section title={'Regulaciones No Arancelarias de Exportacion'} rows={rows} columns={columns} 
                isCollapsed={false} 
                showToolBar={false} hidePagination={true} 
                emptyMessage={"Fraccin sin Regulacin No Arancelaria a la Exportacin."}
                singleColumn={true}
                dropdown={dropdownInfo}
                showSearch={false}
            />
            <ModalTable rows={dropdownInfo} columns={acuerdoColumns} rowDetailsColumns={acuerdoDetailsColumns} onRowClick={onRowClick} />
        </div>
    )
    
}

export default RegulacionesExpo;