import { useState, createContext, useEffect } from "react";
import { fetchData } from "../services/get.service";

export const FractionDetailsContext = createContext({
    fractionDetailsMap: [],
    setfractionDetailsMap: () => null,
    fractionNico: null,
    setFractionNico: () => {}
});

export const FractionDetailsProvider = ({children}) => {
    const [fractionDetailsMap, setfractionDetailsMap] = useState([]);
    const [fractionNico, setFractionNico] = useState(null);

    useEffect(() => {
        fetchData(`fracciondetails&fraccionNico=${fractionNico}`).then(response => {
            if(response) setfractionDetailsMap(response[0]);
        })
    },[fractionNico])

    const value = {fractionDetailsMap, setfractionDetailsMap, fractionNico, setFractionNico};
    return  <FractionDetailsContext.Provider value={value}> {children} </FractionDetailsContext.Provider>
    
}