const API_URL = process.env.REACT_APP_API_BASE_URL;

export const fetchData = async (cmd) => {
    const url = `${API_URL}?cmd=${cmd}`;

    var requestOptions = {
        method: 'GET'
      };

    const response = await fetch(url, requestOptions).catch((err) => {
        return err;
    });

    return response.json();
}