import { useState, createContext, useEffect } from "react";
import { fetchData } from "../services/get.service";

export const FractionInitContext = createContext({
    fractionInitMap: [],
    setfractionInitMap: () => null,
});

export const FractionInitProvider = ({children}) => {
    const [fractionInitMap, setfractionInitMap] = useState([]);

    useEffect(() => {
        fetchData('getLastUpdatedTables').then(response => {
            if(response) {
                const lastUpdatedTables = localStorage.getItem('lastUpdatedTables');

                if(lastUpdatedTables && JSON.stringify(response) === lastUpdatedTables) {
                    const initBasic = localStorage.getItem('initBasic');

                    if(initBasic) {
                        setfractionInitMap(JSON.parse(initBasic));
                    } else {
                        getData();
                    }
                } else {
                    localStorage.setItem('lastUpdatedTables', JSON.stringify(response));
                    getData();
                }
            } else {
                getData();
            }
        })
        
    },[]);

    useEffect(() => {
        console.log(fractionInitMap)
    }, [fractionInitMap])

    const getData = () => {
        fetchData(`initBasic`).then(response => {
            if(response && response['tarifa']) {
                setfractionInitMap(response['tarifa']);
                localStorage.setItem('initBasic', JSON.stringify(response['tarifa']));
            }
        });
    }

    const value = {fractionInitMap, setfractionInitMap};
    return  <FractionInitContext.Provider value={value}> {children} </FractionInitContext.Provider>
    
}