import { styled } from "@mui/material";
import { DataGridPro, GridToolbarQuickFilter } from "@mui/x-data-grid-pro";

export const CMDataGrid = styled(DataGridPro)(({ isTab }) => ({
    '.MuiDataGrid-columnHeaders, .MuiDataGrid-cellContent': {
        // fontSize: '13px',
        color: '#000',
        fontFamily: 'Arial !important',
    },
    '.MuiDataGrid-columnHeaderTitle': {
        fontWeight: 'bold',
    },
    '.MuiDataGrid-cellContent': {
        color: '#333333',
    },
    '.MuiDataGrid-cell:focus': {
        outline: 'none',
    },
    'div[role="row"]': {
        cursor: 'pointer',
    },
    '.even': {
        backgroundColor: '#f9f9f9',
    },
    '.MuiDataGrid-toolbarContainer': {
        display: 'flex',
        justifyContent: 'space-between',
        position: 'absolute',
        bottom: '0',
        lineHeight: '1rem',
    },
    '.MuiFormControl-root': {
        flex: 0.99,
    },
    '.MuiDataGrid-footerContainer': {
        minHeight: 'auto !important',
    },
    '.MuiTablePagination-toolbar': {
        minHeight: 'auto !important',
    },
    '.MuiTablePagination-displayedRows': {
        marginBottom: '0px !important',
    },
    '.MuiDataGrid-footerContainer .MuiIconButton-root': {
        padding: '2px !important',
    },

    '&.single-column-grid': {
        '& .MuiDataGrid-columnHeaders': {
            display: 'none',
        },

        '& .single-row': {
            padding: '0 8px',

            '& > *': {
                marginTop: '5px',
            },

            '& .column-header': {
                fontWeight: 600
            },

            '& .document-icon': {
                left: 0,
                position: 'absolute',
            }
        }
    },

}));

export const CMGridToolbarQuickFilter = styled(GridToolbarQuickFilter)({
    opacity: 1,
    padding: 0,
    width: '100%',
    '& input': {
        // fontSize: 14,
    },
    '.MuiInput-underline::before': {
        borderRadius: '0px',
        border: 'solid 0px #ccc !important',
        paddingTop: '5px',
    },
    '.MuiSvgIcon-root[data-testid="SearchIcon"]': {
        adisplay: 'none !important'
    },
    '.Mui-focused::after': {
        display: 'none !important'
    },
})