import { useState } from "react";
import { CMDocumentContainer, CMDocumentList } from "./document-link.styles";
import { ReactComponent as DocumentsIcon } from "../../../assets/icons/documents.svg";

const SingleLink = ({path, children, ...props}) => {
    
    if(path){
        return <a href={path} target="_blank" {...props}>{children}</a>
    }
    else
    return null;
}

const DocumentLink = ({documents, path, children}) => {
    const [open, setOpen] = useState(false);
    const toggleOpen = () => setOpen(value => !value);

    return (
        <>
        {documents ? 
        <>
            {documents.length > 1 ? (
                <CMDocumentContainer className='document-icon'>
                    <div onClick={toggleOpen}>
                        <DocumentsIcon width={16} height={16}  />
                    </div>
                    {open &&
                    <CMDocumentList>
                        <ul>
                            {
                            documents.map((doc,index) => {
                                const {fileName, filePath} = doc;
                                
                                return (
                                    <li key={index}>
                                        <SingleLink path={filePath}>
                                            {children}
                                            <span>{`${fileName}`}</span>
                                        </SingleLink>
                                    </li>
                                )
                            })
                            }
                        </ul>
                    </CMDocumentList>
                    }
                </CMDocumentContainer>
                ) : (
                    <SingleLink className='document-icon' path={documents[0].filePath}>{children}</SingleLink>
                )
            }
        </> : (
            <SingleLink path={path} className='document-icon'>{children}</SingleLink>
        )
        }
        
        </>
    );
}

export default DocumentLink;