import { useContext, useEffect, useState } from 'react';
import Section from '../common/section/section.component';
import { FractionDetailsContext } from '../../context/fractionDetails.context';
import { FractionInitContext } from '../../context/fractionInit.context';
import DocumentLink from '../common/document-link/document-link.component';
import { ReactComponent as PDFIcon } from '../../assets/icons/pdf.svg';

const PDFColumn = (props) => {
    const {l, fn} = props.row;
    if(l >= 8)
    return <DocumentLink path={`/tarifa-pdf-v5?fn=${fn}`}><PDFIcon width={12} height={12} /></DocumentLink>
}

const columns = [
    {
        field: 'fn',
        headerName: '',
        sortable: false,
    },
    {
        field: 'fp',
        headerName: 'FRACCION',
        description: 'FRACCION',
        width: 100,
        sortable: false,
    },
    { 
        field: 'nc', 
        headerName: 'NICO',
        description: 'NICO',
        width: 60,
        minWidth: 60,
        sortable: false,
    },
    { 
        field: 'd', 
        headerName: 'DESCRIPCION',
        description: 'DESCRIPCION',
        flex: 5,
        minWidth: 300,
    },
    { 
        field: 'cr', 
        headerName: 'Correlacion',
        description: 'Correlacion',
        minWidth: 130,
        sortable: false,
    },
    { 
        field: '', 
        headerName: 'PDF',
        description: 'PDF',
        width: 50,
        minWidth: 30,
        sortable: false,
        renderCell: PDFColumn
    },
    { 
        field: 'um', 
        headerName: 'U.M',
        description: 'U.M',
        width: 55,
        minWidth: 55,
        sortable: false,
    },
    { 
        field: 'im', 
        headerName: 'Imp.',
        description: 'Imp.',
        width: 55,
        minWidth: 55,
        sortable: false,
    },
    { 
        field: 'ex', 
        headerName: 'Exp.',
        description: 'Exp.',
        width: 60,
        minWidth: 60,
        sortable: false,
    },
    { 
        field: 'iva', 
        headerName: 'IVA',
        description: 'IVA',
        width: 50,
        minWidth: 30,
        sortable: false,
    },
    { 
        field: 'dof', 
        headerName: 'DOF',
        width: 90,
        sortable: false,
    },
];

const hiddenColumns = {
    fn: false
}

const Init = () => {
    const [rows, setRows] = useState([]);
    const {fractionNico, setFractionNico} = useContext(FractionDetailsContext);
    const { fractionInitMap } = useContext(FractionInitContext)

    //Function to access the fn from an external script and save it in the local variable that triggers the api request and fills the grids.
    window.updateFractionNico = (fn) => {
        setFractionNico(fn)
    }

    const onRowClickHandler = (params) => {
       setFractionNico(params.row.fn);
    }

    useEffect(() => {
        setRows(fractionInitMap ? fractionInitMap : []);
    },[fractionInitMap]);

    return <Section title={' '} rows={rows} columns={columns} hiddenColumns={hiddenColumns} isCollapsed={false} maxHeight={'585px'} onRowClick={onRowClickHandler}/>;
}

export default Init;