import { useEffect, useState } from 'react';
import { fetchData } from '../../services/get.service';

import Section from '../common/section/section.component';


const columns = [
    /*{
        field: 'id',
        headerName: 'idx',
    },*/
    { 
        field: 'Anexo', 
        headerName: 'Anexo',
        description: 'Anexo',
        flex: 0.1
    },
    { 
        field: 'Apendice', 
        headerName: 'Apendice',
        description: 'Apendice',
        flex: 0.1,
    },
    { 
        field: 'Regimen', 
        headerName: 'Regimen',
        description: 'Regimen',
        flex: 0.4,
    },
    { 
        field: 'Descripcion', 
        headerName: 'Descripcion',
        description: 'Descripcion',
        flex: 0.4,
    },
];

const Anexo22 = () => {
    const [rows, setRows] = useState([]);

    useEffect(() => {
        fetchData("anexo22_select").then(response => {
            if(response) {
                const tmpRows = response.map((item, index) => {
                    return {id: (index+1), ...item}
                });
                setRows(tmpRows);
            }
        })
    },[]);

    return <Section title={'Anexo 22'} rows={rows} columns={columns} isCollapsed={false}/>;
}

export default Anexo22;