import { useContext, useEffect, useState } from "react";
import Section from "../common/section/section.component";
import { FractionDetailsContext } from "../../context/fractionDetails.context";
import DocumentLink from "../common/document-link/document-link.component";
import { ReactComponent as PDFIcon } from '../../assets/icons/pdf.svg';

const DocumentColumn = (props, index) => {
    const {Documentos} = props.row;
    return <DocumentLink key={index} documents={Documentos}>
                <PDFIcon width={16} height={16} />
            </DocumentLink>

}
const columns = [
    {
        field: 'FilePath',
        headerName: '',
        maxWidth:25,
        minWidth: 25,
        renderCell: DocumentColumn
    }, 
    {
        field: 'Fundamento',
        headerName: 'Fundamento',
        description: 'Fundamento',
        flex: 1,
    },
    {
        field: 'Condicion',
        headerName: 'Condicion',
        description: 'Condicion',
        width:100,
    },
    {
        field: 'Tasa',
        headerName: 'Tasa',
        description: 'Tasa',
        width: 100,
    },
    {
        field: 'TasaEspecifico',
        headerName: 'Tasa Especifico',
    },
    {
        field: 'Identificador',
        headerName: 'Identificador',
    },

];


const Ieps = () => {
    const {fractionDetailsMap} = useContext(FractionDetailsContext);
    const [rows, setRows] = useState(fractionDetailsMap);

    useEffect(() => {
        if(fractionDetailsMap['bIEPS']) {
            const tmpRows = fractionDetailsMap['bIEPS'].map((item, index) => {
                return {id: (index+1), ...item}
            });
            setRows(tmpRows);
        } else {
            setRows([]);
        }
    }, [fractionDetailsMap]);

    return <Section title={'IEPS'} rows={rows} columns={columns} 
        isCollapsed={false} 
        showToolBar={false} hidePagination={true} 
        emptyMessage={"Fraccion no aplica IEPS."}
        singleColumn={true}
        />
}
export default Ieps;