import styled from "styled-components";

export const CMSection = styled.div(props => ({
    backgroundColor: 'white',

    '& .panel-heading': {
        padding: '0 !important',

        '& > h4': {
            margin: 0,
            padding: '8px',
            backgroundColor: '#edf2f6',
            border: 'solid 1px #ddd',
            fontWeight: 600,
            minHeight: '12px',
        
            '& a:hover': {
                color: '#000 !important',
                cursor: 'pointer',
                textDecoration: 'underline',
                textShadow: 'unset !important',
            },
        },
    },
    
    '& > div:not(.panel-heading)': {
        height: props.maxHeight ? props.maxHeight : '240px',
        maxHeight: props.maxHeight ? props.maxHeight : '240px',
        overflow: 'auto',
        transition: 'max-height 0.5s ease-out',

        '&.collapsed': {
            maxHeight: 0,
            overflow: "hidden",
        }
    }
}));

export const CMTreeDropdown = styled.div(props => ({
    top: 0,
    right: 30,
    position: 'absolute',
    zIndex: 999,

    '& .dropdown-content': {
        right: '1px',
        maxHeight: '300px',
        overflow: 'auto',
        maxWidth: '500px',
        width: '90vw',

        '& li': {
            display: 'flex',
            whiteSpace: 'pre-wrap'
        },

        '& input': {
            display: 'none',
        },
    }
}))

export const CMDiv = styled.div({
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '80vw',
    maxWidth: '1500px',
    bgcolor: 'background.paper',
    boxShadow: 24,
    p: 4,
    zIndex: 1301,
    backgroundColor: '#fff',
    maxHeight: '80vh',
    overflow: 'hidden',

    [`${CMSection} > div:not(.panel-heading)`]:{
        height: 'calc(80vh + 1rem) !important',
        maxHeight: 'calc(80vh - 32px) !important',
    },
});

export const CMButton = styled.button(props => ({
    backgroundColor: 'unset',
    border: 'unset',
    cursor: 'pointer',
    float: 'right',
    marginTop: '10px',
    paddingLeft: '10px',
    paddingRight: '10px',
    outline: '0 !important',
        
    'svg': {
        transition: 'all 0.5s ease-out',
        transform: !props.collapsed ? 'rotate(180deg)' : 'unset',
    },

    '&.search': {
        '& svg': {
            transform: 'unset'
        }
    }
}));