import { ReactComponent as CalendarIcon } from '../../../assets/icons/calendar.svg';
import { useState } from 'react';
import { MenuItem, Popover, Button } from '@mui/material';
import { CButton, CMButton, CMTextField, Calendar, PagSection } from './calendar.styles';

const CalendarButton = ({ availableDates, onDateChange }) => {
    const itemsPerPage = 5;
    const [anchorEl, setAnchorEl] = useState(null);
    const [filterValue, setFilterValue] = useState('');
    const [filteredDates, setFilteredDates] = useState([]);
    const [page, setPage] = useState(0);
    const uniqueDates = Array.from(new Set(availableDates));

    const handleIconClick = (event) => {
        setAnchorEl(event.currentTarget);
        setPage(0);
        setFilteredDates(uniqueDates);
    };

    const handleFilterChange = (event) => {
        const value = event.target.value.toLowerCase();
        setFilterValue(value);
        const filtered = uniqueDates.filter((date) =>
            date.toLowerCase().includes(value)
        );
        setFilteredDates(filtered);
        setPage(0);
    };

    const handleMenuItemClick = (date) => {
        if (date === 'Seleccionar Todo') {
            date = "";
            onDateChange(date);
        } else {
            onDateChange(date);
        }
    };

    const handleClose = () => {
        setAnchorEl(null);
        setFilterValue('');
    };

    const totalPages = Math.ceil(filteredDates.length / itemsPerPage);
    const startIndex = page * itemsPerPage;
    const slicedDates = filteredDates.slice(startIndex, startIndex + itemsPerPage);

    const handlePageBack = () => {
        setPage((prevPage) => Math.max(0, prevPage - 1));
    };

    const handlePageForward = () => {
        setPage((prevPage) => Math.min(totalPages - 1, prevPage + 1));
    };

    return (
        <div>
            <CButton
                alt="calendar"
                onClick={handleIconClick}
            >
                <CalendarIcon width={25} height={25} />
            </CButton>
            <Popover
                open={Boolean(anchorEl)}
                anchorEl={anchorEl}
                onClose={handleClose}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'left',
                }}
            >
                <CMTextField
                    placeholder="Filtrar fechas"
                    value={filterValue}
                    id='filterValue'
                    onChange={handleFilterChange}
                    size="small"
                />
                <MenuItem onClick={() => handleMenuItemClick('Seleccionar Todo')}>
                    Seleccionar Todo
                </MenuItem>
                {slicedDates.map((date, index) => (
                    <MenuItem key={index} onClick={() => handleMenuItemClick(date)}>
                        {date}
                    </MenuItem>
                ))}
                <PagSection >
                    <Button onClick={handlePageBack} disabled={page === 0}>
                        &lt;
                    </Button>
                    <Button onClick={handlePageForward} disabled={page === totalPages - 1}>
                        &gt;
                    </Button>
                </PagSection>
            </Popover>
        </div>
    );
};

export default CalendarButton;
