import styled from "styled-components";

export const CMDocumentList = styled.div({ 
    width: 'max-content',
    padding: '1rem',
    display: 'inline-block',
    background: 'white',
    position: 'absolute',
    top: 'calc(100% + 5px)',
    borderRadius: '8px',
    boxShadow: 'rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px',

    '& ul': {
        margin: 0,
        paddingInlineStart: 0,

        '& li': {
            listStyle: 'none',

            '& svg': {
                marginRight: '1rem',
            },
            
            '& a': {
                color: 'black',

                '&:hover': {
                    color: '#007bff'
                }
            }
        }
    }
});

export const CMDocumentContainer = styled.div({
    position: 'relative',
})