import { useContext, useEffect, useState } from "react";
import Section from "../common/section/section.component";
import { FractionDetailsContext } from "../../context/fractionDetails.context";

const columns = [
    {
        field: 'Anexo',
        headerName: 'Anexo',
    },
    {
        field: 'Sector',
        headerName: 'Sector',
    },
    {
        field: 'Regimen',
        headerName: 'Regimen',
    },
    {
        field: 'Condicion',
        headerName: 'Condicion',
    },
    {
        field: 'Aduanas',
        headerName: 'Aduanas',
    },
    {
        field: 'DOF',
        headerName: 'DOF',
    },
];


const Rcg2 = () => {
    const {fractionDetailsMap} = useContext(FractionDetailsContext);
    const [rows, setRows] = useState(fractionDetailsMap);

    useEffect(() => {
        if(fractionDetailsMap['bRcg2']) {
            const tmpRows = fractionDetailsMap['bRcg2'].map((item, index) => {
                return {id: (index+1), ...item}
            });
            setRows(tmpRows);
        } else {
            setRows([]);
        }
    }, [fractionDetailsMap]);

    return <Section title={'Anexos de las Reglas de Comercion Exterior'} rows={rows} columns={columns} 
        isCollapsed={false} 
        showToolBar={false} hidePagination={true} emptyMessage={"Fraccion sin cumplimiento de los Anexos de las Reglas."}
        singleColumn={true}
        />
}
export default Rcg2;