import { useContext, useEffect, useState } from "react";
import Section from "../common/section/section.component";
import { FractionDetailsContext } from "../../context/fractionDetails.context";
import DocumentLink from "../common/document-link/document-link.component";
import { ReactComponent as PDFIcon } from '../../assets/icons/pdf.svg';

const DocumentColumn = (props, index) => {
    return <DocumentLink key={index} path={`${props.row.FilePath}`}><PDFIcon width={16} height={16} /></DocumentLink>
}

const columns = [
    {
        field: 'Documento',
        headerName: '',
        minWidth: 25,
        maxWidth: 25,
        renderCell: DocumentColumn
    },
    {
        field: 'Rango',
        headerName: 'Rango',
    },
    {
        field: 'DOF',
        headerName: 'DOF',
    },
    {
        field: 'ReglaAplicable',
        headerName: 'Regla Aplicable',
    },
    {
        field: 'NotaSeccion',
        headerName: 'Nota Seccion',
    },
    {
        field: 'NotaCapitulo',
        headerName: 'Nota Capitulo',
    },
    {
        field: 'Notas',
        headerName: 'Notas',
    },
];


const ReglasOrigen = () => {
    const {fractionDetailsMap} = useContext(FractionDetailsContext);
    const [rows, setRows] = useState(fractionDetailsMap);

    useEffect(() => {
        if(fractionDetailsMap['bReglasOrigen']) {
            const tmpRows = fractionDetailsMap['bReglasOrigen'].map((item, index) => {
                return {id: (index+1), ...item}
            });
            setRows(tmpRows);
        } else {
            setRows([]);
        }
    }, [fractionDetailsMap]);

    return <Section title={'Reglas de Origen'} rows={rows} columns={columns} 
        isCollapsed={false} 
        showToolBar={false} hidePagination={true} emptyMessage={"Fraccion no aplica Reglas de Origen."}
        singleColumn={true}
        />
}
export default ReglasOrigen;