import { useContext, useEffect, useState  } from 'react';
import Section from '../common/section/section.component';
import { FractionDetailsContext } from "../../context/fractionDetails.context";
import { ReactComponent as PDFIcon } from "../../assets/icons/pdf.svg";
import DocumentLink from "../common/document-link/document-link.component";


const docCol = (props) => {
    const {Documento} = props.row;
    return <DocumentLink path={`/docs/cupos/${Documento}`}><PDFIcon width={12} height={12}></PDFIcon></DocumentLink>    
}

const columns = [
    {
        field: 'Documento',
        headerName: '',
        description: 'Documento',
        maxWidth: 25,
        minWidth: 25,
        renderCell: docCol,
    },
    {
        field: 'DOF',
        headerName: 'DOF',
        description: 'DOF',
        width: 80,
    },
    {
        field: 'Condicion',
        headerName: 'Condicion',
        description: 'Condicion',
        flex: 1,
        minWidth: 75,
    },
    {
        field: 'Permiso',
        headerName: 'Permiso',
        description: 'Permiso',
        width: 80,
    },
    {
        field: 'Vigencia',
        headerName: 'Vigencia',
        description: 'Vigencia',
        flex: 0.25,
        minWidth: 75,
    },
];

const extraColumnsInPopup = [
    {
        field: 'Pais',
        headerName: 'Pais',
        description: 'Pais',
        width: 100,
    },
    {
        field: 'Observaciones',
        headerName: 'Observaciones',
        description: 'Observaciones',
        flex: 1,
    }
];


const Cupo = () => {
    const {fractionDetailsMap} = useContext(FractionDetailsContext);
    const [rows, setRows] = useState(fractionDetailsMap);

    useEffect(() => {
        if(fractionDetailsMap['bCupo']) {
            const tmpRows = fractionDetailsMap['bCupo'].map((item, index) => {
                return {id: (index+1), ...item}
            });
            setRows(tmpRows);
        } else {
            setRows([]);
        }
    },[fractionDetailsMap]);

    
    return <Section title={'Cupos'} rows={rows} columns={columns} extraColumnsInPopup={extraColumnsInPopup} isCollapsed={false} 
        showToolBar={false} hidePagination={true} emptyMessage={"Fraccion sin el cumplimiento de un cupo"} />;
}

export default Cupo;