import { useEffect, useState } from 'react';
import { fetchData } from '../../services/get.service';
import { ReactComponent as PDFIcon } from '../../assets/icons/pdf.svg';
import Section from '../common/section/section.component';
import DocumentLink from '../common/document-link/document-link.component';

const PDFColumn = (props) => {
    const iconStyle = {
        boxShadow: '0px 1px 0px 0px rgba(247, 7, 7,0.5)',

    };

    return (
        <DocumentLink path={props.row.FilePath}>
            <PDFIcon width={12} height={12} style={iconStyle} />
        </DocumentLink>
    );
};

const DateColumn = (props) => {
    const row = props.row;
    return (
        <span>
            <span style={{ fontWeight: 'bold' }}>{row.Fecha}</span>
            {row.Secretaria && (
                <span>
                    {' '}
                    - <span style={{ fontWeight: 'bold' }}>{row.Secretaria}</span>
                </span>
            )}
        </span>
    );
};

const columns = [
    {
        field: 'Publicacion',
        headerName: 'Publicacin',
        description: 'Publicacin',
        flex: 1,
        renderCell: (props) => {
            const row = props.row;
            return (
                <div className='MuiDataGrid-cellContent'>
                    <PDFColumn row={row} />
                    <DateColumn row={row} />
                    - <span>{row.Publicacion}</span>
                </div>
            );
        },
    },
];


const Bitacora = () => {
    const [rows, setRows] = useState([]);
    const [availableDates, setAvaliableDates] = useState([]);
    const [selectedDate, setSelectedDate] = useState('');

    useEffect(() => {
        fetchData("bitacora_select").then(response => {
            if (response) {
                const tmpRows = response.map((item, index) => {
                    return { id: (index + 1), ...item }
                });
                setRows(tmpRows);

                const fechas = response.map(item => item.Fecha);
                setAvaliableDates(fechas);
            }
        })
    }, []);

    const handleDateChange = (date) => {
        setSelectedDate(date === selectedDate ? '' : date);
    };

    const filteredRows = selectedDate ? rows.filter(row => row.Fecha === selectedDate) : rows;

    return <Section
        title={'Bitacora de Publicaciones DOF'}
        rows={filteredRows}
        columns={columns}
        isCollapsed={false}
        availableDates={availableDates}
        onDateChange={handleDateChange}
        showCalendar={true}
    />;
}

export default Bitacora;
