import { useEffect, useState } from 'react';
import { fetchData } from '../../services/get.service';
import { ReactComponent as PDFIcon } from '../../assets/icons/pdf.svg';
import { ReactComponent as DOCIcon } from '../../assets/icons/doc.svg';
import Section from '../common/section/section.component';
import DocumentLink from '../common/document-link/document-link.component';
import { TabPanel } from '../common/tabs/tabs.component';
import { TABS_DATA } from '../../services/data/tabs';

const LinkColumn = (props) => {
    return (
        <div key={props.row.id}>
            <div className="column-class">
                {props.row.link2 ? (
                    <>
                        <a target="__blank" href={props.row.link1}>{props.row.title1}</a>
                        <br />
                        <a target="__blank" href={props.row.link2}>{props.row.title2}</a>
                    </>
                ) : (
                    <a target="__blank" href={props.row.link1}>{props.row.title1}</a>
                )}
            </div>
        </div>
    );
};

const tabs = ["HTS", "Leyes", "Acuerdos", "Resoluciones", "Reglas y Reglamentos", "Otros"];
const tabColumns = [
    {
        field: 'Name',
        headerName: 'Nombre',
        description: 'Nombre',
        flex: 1,
        renderCell: LinkColumn
    }
]
const CombinedColumn = (props) => {
    return (
        <div key={props.row.id} className="combined-column">
            <div className="icon-container">
                {props.row.PathPdf && (
                    <DocumentLink path={props.row.PathPdf}><PDFIcon width={12} height={12} /></DocumentLink>
                )}
                {props.row.PathDoc && (
                    <DocumentLink path={props.row.PathDoc}><DOCIcon width={12} height={12} /></DocumentLink>
                )}
            </div>
            <span className="name">{props.row.Name}</span>
        </div>
    );
};

const columns = [
    {
        field: 'Name',
        headerName: 'Nombre',
        description: 'Nombre',
        flex: 1,
        renderCell: CombinedColumn
    },
];

const Leyesyreglamentos = () => {
    const [rows, setRows] = useState([]);
    const [selectedTab, setSelectedTab] = useState(0);
    const [isShowModal, setIsShowModal] = useState(false);
    useEffect(() => {
        fetchData("leyesyreglamentos_select").then(response => {
            if (response) {
                const tmpRows = response.map((item, index) => {
                    return { id: (index + 1), ...item }
                });
                console.log({ "Este ESS ": tmpRows });
                setRows(tmpRows);
            }
        })
    }, []);
    return <div>
        <TabPanel value={selectedTab} index={0}>
            <Section title={'Leyes y Reglamentos'}
                isShowModal={isShowModal}
                setIsShowModal={setIsShowModal}
                tabLabels={tabs}
                isTab={true}
                setSelectedTab={setSelectedTab}
                selectedTab={selectedTab}
                rows={rows}
                columns={columns}
                isCollapsed={false} />;
        </TabPanel>
        <TabPanel value={selectedTab} index={1}>
            <Section title={TABS_DATA.leyes.tabName}
                isShowModal={isShowModal}
                setIsShowModal={setIsShowModal}
                tabLabels={tabs}
                isTab={true}
                setSelectedTab={setSelectedTab}
                selectedTab={selectedTab}
                rows={TABS_DATA.leyes.rows}
                columns={tabColumns}
                isCollapsed={false} />;
        </TabPanel>
        <TabPanel value={selectedTab} index={2}>
            <Section title={TABS_DATA.acuerdos.tabName}
                isShowModal={isShowModal}
                setIsShowModal={setIsShowModal}
                tabLabels={tabs}
                isTab={true}
                setSelectedTab={setSelectedTab}
                selectedTab={selectedTab}
                rows={TABS_DATA.acuerdos.rows}
                columns={tabColumns}
                isCollapsed={false} />;
        </TabPanel>
        <TabPanel value={selectedTab} index={3}>
            <Section title={TABS_DATA.resoluciones.tabName}
                isShowModal={isShowModal}
                setIsShowModal={setIsShowModal}
                tabLabels={tabs}
                isTab={true}
                setSelectedTab={setSelectedTab}
                selectedTab={selectedTab}
                rows={TABS_DATA.resoluciones.rows}
                columns={tabColumns}
                isCollapsed={false} />;
        </TabPanel>
        <TabPanel value={selectedTab} index={4}>
            <Section title={TABS_DATA.reglasyreglamentos.tabName}
                isShowModal={isShowModal}
                setIsShowModal={setIsShowModal}
                tabLabels={tabs}
                isTab={true}
                setSelectedTab={setSelectedTab}
                selectedTab={selectedTab}
                rows={TABS_DATA.reglasyreglamentos.rows}
                columns={tabColumns}
                isCollapsed={false} />;
        </TabPanel>
        <TabPanel value={selectedTab} index={5}>
            <Section title={TABS_DATA.otros.tabName}
                isShowModal={isShowModal}
                setIsShowModal={setIsShowModal}
                tabLabels={tabs}
                isTab={true}
                setSelectedTab={setSelectedTab}
                selectedTab={selectedTab}
                rows={TABS_DATA.otros.rows}
                columns={tabColumns}
                isCollapsed={false} />;
        </TabPanel>


    </div>
}

export default Leyesyreglamentos;